.outdoorExperience{
    display: grid;
    background-color: white;
    padding: 4%;
    border-radius: 10px;
    gap: 2.5%;
    /* grid-template-columns: repeat(4, 1fr); */
    /* grid-template-rows: auto; */
    grid-template-areas: 
    "leftImage . . leisureButton"
    "leftImage title title ."
    "leftImage text text text"
    "leftImage subImage1 subImage2 subImage3";
}

.outdoor__leftImage{
    grid-area: leftImage;
    width: auto;
    height: 100%;
    border-radius: 10px;
}

.outdoor__LeisureButton{
    grid-area: leisureButton;
    margin: 0;
    background-color: #005AEE;
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    color: #FFFFFF;
}

.outdoor__title{
    grid-area: title;
    font-size: 3em;
    margin: 0;
}

.outdoor__text{
grid-area: text;
}

.outdoor__subimage1, .outdoor__subimage2, .outdoor__subimage3{
    /* Can't make it fill only the box */
    border-radius: 10px;
}

.outdoor__subimage1{
    grid-area: subImage1;
}

.outdoor__subimage2{
    grid-area: subImage2;
}
.outdoor__subimage3{
    grid-area: subImage3;
}
